@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



:root {
    --montserrat-font: "Montserrat", sans-serif;

    --yoda-gradient: linear-gradient(-45deg, #ff0099, #493240);
    --pigypink-gradient: linear-gradient(45deg, #ee9ca7, #f0abb4fb, #ffdde1);
    --forest-gradient: linear-gradient(to right, #000428, #004e92);
    --dull-gradient: linear-gradient(to right, #c9d6ff, #e2e2e2);
    --mystic-gradient: linear-gradient(65deg, #757f9a, #d7dde8);
    --khaki-gradient: linear-gradient(
        to right bottom,
        #e0a015,
        #f0b000,
        #ebc748,
        #fadf87,
        #fcda7dda
    );

    --midnightCity-gradient: linear-gradient(to bottom, #232526, #414345);
    --deepSpace-gradient: linear-gradient(to bottom, #000000, #434343);
}

a {
    text-decoration: none;
    color: inherit;
}

.nav-links {
    display: flex;
    list-style: none;
    flex: 1 1 auto;
}

.nav-links li {
    height: 100%;
    padding: 0 20px;
}

.nav-links li a {
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.3em;
    color: white;
}

.logo{
    width: 55px;
    padding: 5px 0;
}
.btn {
    outline: none;
    border: none;
    background: transparent;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 20px;
    font-weight: bold;
    cursor: pointer;
}

.outline {
    border: 1px solid black;
    border-radius: 5px;
}

.contained {
    border-radius: 5px;
}

.dark {
    background: #1f1f1f;
}



.light {
    background: rgb(245, 245, 245);
}

.red {
    background: rgb(236, 2, 119);
}

.blue {
    background: rgb(0, 177, 247);
}

.green {
    background: rgb(0, 255, 179);
}

.large {
    font-size: 2em;
}

.medium {
    font-size: 1.2em;
}

.small {
    font-size: 0.8em;
}

.accessories-container {
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.accessories-container img {
    width: 100%;
    height: 100vh;
    opacity: 0.7;
}

.accessories-btn {
    position: absolute;
}

@media (max-width: 1200px) {
    .accessories-container img {
        width: 100%;
        height: unset;
    }

}

.home-header-container {
    margin-top: 70px;
    position: relative;
    overflow: hidden;
}

.header-img-wrapper {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-header-img {
    transition: 0.5s;
}
.home-header-img:hover {
    opacity: 0.9;
    transition: 0.5s;
}

.home-header-content-wrapper {
    position: absolute;
    color: white;
    text-transform: uppercase;
    top: 50%;
    font-size: 1.8em;
    font-weight: bolder;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.home-header-button-wrapper {
    margin-top: 15px;
}

@media (max-width: 1200px) {
    .home-header-img {
        height: unset;
    }

    .home-header-content-wrapper {
        font-size: 1.3em;
    }
}
@media (max-width: 440px) {
    .home-header-content-wrapper {
        font-size: 1em;
    }
}

.scroll-arrow {
    font-size: 2em !important;
    cursor: pointer;
    margin: 0 10px;
}

.scroling-picture {
    padding: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
}

.scroling-picture img {
    opacity: 0.9;
}

.scroling-picture:hover {
    opacity: 0.89;
    transition: 1s;
}

.scrolling-img-btn {
    position: absolute;
}
.scrolling-img-btn > * {
    font-weight: bolder !important;
    text-transform: capitalize;
}

.menu-wrapper {
}

.WMHero-Container {
    overflow-x: hidden;
}

.hero-img-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
}

.WHHero-label-man {
    position: absolute;
    top: 50%;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    transition: 0.8s;
    cursor: pointer;
    font-size: 7em;
    text-decoration: line-through;
}
.WHHero-label-woman {
    position: absolute;
    top: 50%;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    transition: 0.5s;
    cursor: pointer;
    font-size: 7em;
    text-decoration: line-through;
}

.WHHero-label-man:hover {
    color: black;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    transition: 0.5s;
    text-decoration: none;
}

.WHHero-label-woman:hover {
    color: black;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    transition: 0.5s;
    text-decoration: none;
}


@media(max-width:510px){
    .WHHero-label-man {
   
    font-size: 4em;
}
.WHHero-label-woman {

    font-size: 4em;
}
}

.products-container {
    display: flex;
    margin: auto;
    margin: 150px 0;
    width: 100%;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.product-img {
    width: 250px;
    height: 350px;
    transition: 1s ease;
}

.product-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.product-card:hover .product-img {
    width: 270px;
    transform: translateY(-10px);
}

.product-card:hover .product-title {
    font-size: 1.05em;
    transition: 0.5s;
}

.product-title {
    width: 250px;
    text-align: center;
    padding: 15px 15px;
    border-radius: 5px;
    background: rgb(247, 247, 247);
    margin-bottom: 15px;
    font-size: 1em;
    transition: 0.5s;
}

.product-category {
    text-transform: capitalize;
    color: rgb(1, 52, 100);
    padding: 10px 0;
}
.product-category span {
    color: rgb(100, 100, 100);
}

.product-price {
    padding-bottom: 10px;
    color: rgb(18, 4, 136);
}

.product-price span {
    color: rgb(100, 100, 100);
}
.mrl {
    margin: 0 10px !important;
}

/*  product preloader */
.pp-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15vh;
    overflow: hidden;
}

.pp-item {
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
}


.footer-container {
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    background: var(--midnightCity-gradient);
}

.footer-social-wrapper {
    width: 100%;
    display: flex;
    padding: 20px;
    justify-content: center;
    align-items: center;
}

.footer-email-subscribe {
    width: 100%;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.saysomthing {
    background: var(--pigypink-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1em;
    text-transform: capitalize;
}
#footer-form {
    width: 80%;
    display: flex;
    justify-content: center;
    margin: auto;
    align-items: center;
    /* flex-wrap: wrap; */
    margin: 10px 0;
}

#footer-email-label {
    font-size: 2em;
    color: white;
    margin-right: 10px;
    padding: 5px;
}

#footer-email-input {
    min-width: 100px;
    width: 30%;
    border-radius: 0;
    outline: none;
    border: none;
    padding: 5px;
    height: 3vh;
    text-transform: capitalize;
    margin: 5px;
}

.submit-footer-form-btn {
    margin: 5px;
}
.footer-social-icon {
    transition: 1s;
    margin: 3%;
}
.footer-social-icon:hover {
    transform: translateY(-8px);
    transition: 1s;
}

.footer-contact-info {
    color: white;
    display: flex;
    flex-direction: column;
    border: 2px groove rgb(197, 197, 197);
    padding: 10px 15px;
    margin: 15px 0;
}

.footer-contact-info span {
    display: flex;
    margin: 5px 0;
}

.footer-contact-info span h5 {
    margin-right: 5px;
}

.footer-contact-info span p {
    letter-spacing: 0.1em;
}

.footer-outro {
    width: 100%;
    display: flex;
    color: whitesmoke;
    justify-content: center;
    margin: auto;
    align-items: center;
    font-size: 0.7em;
    height: 5vh;
    position: absolute;
    bottom: 0;
}

.footer-outro a {
    text-decoration: none;
    color: whitesmoke;
    transition: 0.5s;
    font-size: 1.1em;
}

.footer-outro a:hover {
    color: rgb(255, 0, 119);
    transition: 0.5s;
}

.footer-outro p {
    margin: 0 5px;
    font-size: 1.1em;
    font-weight: 200;
    letter-spacing: 0.2em;
}
@media (max-width: 400px) {
    .footer-contact-info {
        font-size: 0.8em;
    }
    #footer-form {
        width: 90%;
    }
    #footer-form * {
        font-size: 1em;
    }
    #foote-email-label {
        font-size: 1em;
    }
    #footer-email-input {
        font-size: 0.5em;
        width: 90%;
    }
    .submit-footer-form-btn * {
        font-size: 0.9em !important;
    }

    .saysomthing {
        font-size: 1em;
    }
}

.error-container{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: black;
    overflow: hidden;
}

.error-img{
   

}

@media(max-width:500px){
    .error-img{
        width: 100%;
      
    }
}
